import Vue from 'vue'
import Router from 'vue-router'

import Todo from "./components/todo/Todo.vue"
import Board from "./components/board/Board.vue"
import AppUser from "./components/user/AppUser.vue"

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/todo',
            name: 'Todo',
            component: Todo
        },
        {
            path: '/board',
            name: 'Board',
            component: Board
        },
        {
            path: '/user',
            name: 'AppUser',
            component: AppUser
        },
    ],
    
});
