<template>
    <div id="boardBody" class="container">
        <b-table id="boardList" 
            striped hover 
            :items="items" 
            :fields="fields" 
            :per-page="perPage"
            :current-page="currentPage"
            @row-clicked="handleClick"
            ></b-table>
        <div class="container pagination justify-content-center">
            <b-pagination
                v-model="currentPage"
                :total-rows="rows"
                :per-page="perPage"
                aria-controls="boardList"
                first-number
                last-number></b-pagination>
        </div>
        <b-modal ref="detail-modal" title="게시글 상세보기" @ok="updateBoard">
            <b-input-group prepend="id" class="my-4">
                <b-form-input class="detail" readonly :value="detailViewId[0]"></b-form-input>
            </b-input-group>
            <b-input-group prepend="WriteDate" class="my-4">
                <b-form-input class="detail" readonly :value="detailViewWriteDate[0]"></b-form-input>
            </b-input-group>
            <b-input-group prepend="Title" class="my-4">
                <b-form-input id="title" class="detail" readonly :value="detailViewTitle[0]"></b-form-input>
            </b-input-group>
            <b-input-group prepend="Content" class="my-4">
                <b-form-textarea id="content" class="detail" readonly :value="detailViewContent[0]"></b-form-textarea>
            </b-input-group>
            <b-form inline-block>
                <b-form-group>
                    <b-button class="mb-2 mr-sm-2 mb-sm-0" block @click="deleteBoard">
                        <i class="far fa-trash-alt" aria-hidden="true"></i>
                    </b-button>
                    <b-button class="mb-2 mr-sm-2 mb-sm-0" block @click="modifyBoard">
                        <i class="fas fa-edit" aria-hidden="true"></i>
                    </b-button>
                </b-form-group>
            </b-form>
        </b-modal>
    </div>
</template>

<script>
var list = [];
var boardNum = [];
export default {
    methods: {
        getList(){
            // window.console.log(location.origin);
            this.$axios.get("http://localhost:8080/board", { 
                headers:{
                    'Access-Control-Allow-Origin': '*',
                }
             })
            .then((res)=>{
                var data = JSON.parse(JSON.stringify(res.data));
                data.forEach((row)=>{
                    this.items.push(row);
                })
                // self.items = items;
            })
            .catch((err)=>{
                window.console.log("에러 : "+err);
            })
        },
        handleClick(record){
            // modal 오픈
            this.resetBoard();
            this.$refs['detail-modal'].show();
            window.console.log(record.boardNum);
            this.$axios.get('http://localhost:8080/board/detail/'+record.boardNum,{
                headers:{
                    'Access-Control-Allow-Origin': '*',
                }
            })
                .then((res)=>{
                    var data = JSON.parse(JSON.stringify(res.data));
                    window.console.log(data);
                    this.detailViewId.push(data.id);
                    this.detailViewTitle.push(data.title);
                    this.detailViewContent.push(data.content);
                    this.detailViewWriteDate.push(data.writeDate);
                    this.boardNum.push(data.boardNum);
                })
                .catch((err)=>{
                    window.console.log(err);
                })
        },
        deleteBoard(){
            // window.console.log(this.boardNum);
            this.$axios.post('http://localhost:8080/board/delete/'+this.boardNum[0])
                .then((res)=>{
                    this.items = [];
                    this.$refs['detail-modal'].hide();
                    this.getList();
                })
                .catch((err)=>{
                    window.console.log(err);
                })
        },
        modifyboard(){
            document.getElementById('title').readOnly = false;
            document.getElementById('content').readOnly = false;
        },
        resetBoard(){
            this.detailViewId = [];
            this.detailViewTitle = [];
            this.detailViewWriteDate = [];
            this.detailViewContent = [];
            this.boardNum = [];
        },
        updateBoard(bvModalEvt){
            bvModalEvt.preventDefault()
            this.$axios.post('http://localhost:8080/board/update/'+this.boardNum[0]+'/'+this.detailViewTitle+'/'+this.detailViewContent)
                .then((res)=>{
                    window.console.log(res);
                })
                .catch((err)=>{
                    window.console.log(err);
                })
        }
        
    },
    mounted() {
        this.getList()
    },
    data(){
        return{
            perPage:5,
            currentPage: 1,
            fields: [
                {
                    key: 'boardNum',
                    sortable: false
                },
                {
                    key: 'title',
                    sortable: false,
                },
                {
                    key: 'id',
                    sortable: true
                },
                {
                    key: 'writeDate',
                    sortable: true
                },
            ],
            items: [],
            detailViewId : [],
            detailViewTitle : [],
            detailViewWriteDate : [],
            detailViewContent : [],
        }
    },
    computed:{
        rows() {
            return this.items.length
        }
    }
}
</script>

