<template>
    <div id="todo">
      <TodoHeader/>
      <TodoForm v-on:addTodo="addTodo"></TodoForm>
      <TodoList v-bind:propsdata="todoItems" @removeTodo="removeTodo">
      </TodoList>
      <TodoFooter v-on:removeAll="clearAll"></TodoFooter>
    </div>
</template>

<script>
// import axios from 'axios';
import TodoForm from './todoForm.vue'
import TodoHeader from './todoHeader.vue'
import TodoList from './todoList.vue'
import TodoFooter from './todoFooter.vue'

var url = 'http://localhost:8080'
export default {
  data() {
    return{
      todoItems: [],
    }
  },
  created() {
    this.$axios.get(url+'/todo')
      .then((res)=>{
        for(var i=0; i<res.data.length; i++){
          this.todoItems.push(res.data[i]);
        }
      })
      .catch()
  },
  methods: {
    addTodo(todoItem) {
      this.$axios.post(url+'/todo/insert/'+todoItem)
        .then((res)=>{
          this.todoItems = [];
          for(var i=0; i<res.data.length; i++){
            this.todoItems.push(res.data[i]);
          }
        })
        .catch()
        
    },
    clearAll() {
      this.$axios.post(url+'/todo/deleteAll')
        .then()
        .catch()
      this.todoItems = [];
      // this.todoIds = [];
    },
    removeTodo (todoItem, id) {
      this.$axios.post(url+'/todo/delete/'+id)
        .then((res)=>{
          this.todoItems = [];
          for(var i=0; i<res.data.length; i++){
            this.todoItems.push(res.data[i]);
          }
        })
        .catch()
    },
  },
  components: {
    TodoHeader,
    TodoList,
    TodoForm,
    TodoFooter,
  },
}
</script>

