<template>
    <div class="container">
        <!-- 검색 form -->
        <b-form inline @submit="onSubmit" @reset="onReset" v-if="show" class="form-horizontal">
            <b-form-group>
                <b-form-select class= "mb-2 mr-sm-2 mb-sm-0" id="searchType" v-model="form.searchType" :options="searchType" required>
                </b-form-select>
                <b-form-input class="mb-2 mr-sm-2 mb-sm-0" id="searchContent" v-model="form.content" placeholder="검색어를 적어주세요."></b-form-input>
                <b-button type="submit">Search</b-button>
            </b-form-group>
        </b-form>
    </div>
</template>

<script>
  export default {
    data() {
      return {
        form: {
          searchType: null,
          searchContent: '',
        },
        searchType: [{ text: '분류 기준', value: null }, { text: '제목', value: 'title' },{ text: '작성자', value: 'id' },],
        show: true
      }
    },
    methods: {
      onSubmit(event) {
        event.preventDefault()
        alert(JSON.stringify(this.form))
      },
      onReset(event) {
        event.preventDefault()
        // Reset our form values
        this.form.email = ''
        this.form.name = ''
        this.form.food = null
        this.form.checked = []
        // Trick to reset/clear native browser form validation state
        this.show = false
        this.$nextTick(() => {
          this.show = true
        })
      }
    },

  }
</script>

<style scoped>
  .container {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 1%;
  }
  .form-group{
    margin : auto;
  }
</style>