<template>
  <section>
    <transition-group name="list" tag="ul">
      <!-- transition-group으로 DOM의 삽입, 삭제, 갱신등의 효과를 높임 -->
      <li v-for="(todoItem, index) in propsdata" :key="index" class="shadow">
        <i class="checkBtn fas fa-check" aria-hidden="true" @click="checkTodo(index)"></i>
        <p class="todoItem">
          {{ todoItem.content }}
        </p>
        <span class="removeBtn" type="button" @click="removeTodo(todoItem, todoItem.id)">
          <i class="far fa-trash-alt" aria-hidden="true"></i>
        </span>
      </li>
    </transition-group>  
  </section>
</template>

<script>
var clickTimes=0;
export default {
  props: ['propsdata'],
  data() {
    return {
      modifiedItem: '',
      // todoIds:[],
      index: 0,
    }
  },
  methods : {
    removeTodo (todoItem, id) {
      this.$emit('removeTodo', todoItem, id)
    },
    checkTodo(index){
      var todo = document.getElementsByClassName("todoItem")[index];
      var btn = document.getElementsByClassName("checkBtn")[index];
      clickTimes++;

      if(clickTimes%2==1){
        todo.style.textDecoration="line-through";
        btn.style.color="red";
      }else{
        todo.style.textDecoration="none";
        btn.style.color="skyblue";
      }
    }
    
  },
  components:{

  }
}
</script>

<style scoped>
.checkBtn {
  cursor: pointer;
  color: skyblue;
}

.list-enter-active, .list-leave-active {
  transition: all 1s;
}

.list-enter, .list-leave-to {
  opacity: 0;
  transform: translateY(30px);
}
ul {
  list-style-type: none;
  padding-left: 0px;
  margin-top: 0px;
  text-align: left;
}

li {
  display: flex;
  min-height: 50px;
  height: 50px;
  line-height: 50px;
  margin: 0.5rem 0;
  padding: 0 0.9rem;
  background: white;
  border-radius: 5px;
}

.checkBtn {
  line-height: 45px;
  color: #62acde;
  margin-right: 10px;
}

.removeBtn {
  margin-left: auto;
  color: #de4343;
}
.modifiedBtn{
  margin-left: 30px;
  color: blue;
}
input:focus {
  outline: none;
}
.inputBox {
  background: white;
  height: 50px;
  line-height: 50px;
  border-radius: 5px;
}
.inputBox input {
  border-style: none;
  font-size: 1.0rem;
  text-align: center;
}
.addContainer {
  float: right;
  background: linear-gradient(to right, #6478FB, #8763FB);
  display: block;
  width: 3rem;
  border-radius: 0 5px 5px 0;
}
.addBtn {
  color: white;
  vertical-align: middle;
}
</style>