<template>
  <!-- create board Modal -->
        <div class="container">
            <b-button v-b-modal.modal-prevent-closing>글쓰기</b-button>
            <b-modal
                id="modal-prevent-closing"
                ref="modal"
                title="게시글 작성"
                @show="resetModal"
                @ok="handleOk">
                <b-form inline v-on:submit.prevent="boardCreate">
                  <b-input-group class="input-group mt-3 w-100 mx-auto" prepend="id">
                    <b-form-input id="id" v-model="id" required></b-form-input>
                  </b-input-group>
                  <b-input-group class="input-group mt-3 w-100 mx-auto" prepend="title">
                    <b-form-input id="title" v-model="Title" required></b-form-input>
                  </b-input-group>
                  <b-input-group class="input-group mt-3 w-100 mx-auto" prepend="content">
                    <b-form-textarea id="content" v-model="content" required></b-form-textarea>
                  </b-input-group>
                </b-form>
            </b-modal>
        </div>
</template>

<script>
export default {
    data() {
      return {
        id:'',
        title:'',
        content:'',
      }
    },
    methods: {
      resetModal() {
        this.id = ''
        this.title = ''
        this.content = ''
      },
      handleOk(bvModalEvt) {
        // Prevent modal from closing
        bvModalEvt.preventDefault()
        // Trigger submit handler
        this.boardCreate()
      },
      boardCreate() {
        // if(this.id != null && this.title != null && this.content != null){
          var data = {
            id : this.id,
            title : this.title,
            content : this.content,
          }
          // window.console.log(data)
          this.$emit('boardCreate',data)
        // }
        // Hide the modal manually
        this.$nextTick(() => {
          this.$bvModal.hide('modal-prevent-closing')
        })
      }
    }
}
</script>

<style scoped>
.container {
  margin-bottom: 1%;
  float: right;
}
.btn-secondary {
    float: right;
}
.input-group {
  margin-bottom: 1%;
}
</style>