<template>
    <div class="inputBox shadow">
        <input size="70" type="text" v-model="newTodoItem" placeholder="할 일을 입력하세요" v-on:keyup.enter="addTodo">
        <span class="addContainer" v-on:click="addTodo">
            <i class="addBtn fas fa-plus" aria-hidden="true"></i>
        </span>
    </div>
</template>

<script>
export default {
  data: function() {
    return{
        newTodoItem: '',
    }
  },
  methods: {
    addTodo() {
        if (this.newTodoItem !== ''){
            // var value = this.newTodoItem && this.newTodoItem.trim()
            // trim : 양쪽 공백 제거, emit : event 전달
            this.$emit('addTodo', this.newTodoItem && this.newTodoItem.trim()); //addTodo 이벤트 발생
            this.clearInput();  //이벤트 후 input태그 비우기
        } else {
            window.alert("warn : 할 일을 입력해주세요");
        }
    },
    clearInput() {
        this.newTodoItem = '';
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  input:focus {
    outline: none;
  }
  .inputBox {
    background: white;
    height: 50px;
    line-height: 50px;
    border-radius: 5px;
  }
  .inputBox input {
    border-style: none;
    /* font-size: 1.0rem; */
    text-align: center;
    background: white;
    height: 50px;
    /* line-height: 50px;
    border-radius: 5px; */
  }
  .addContainer {
    float: right;
    background: black;
    display: block;
    width: 3rem;
    border-radius: 0 5px 5px 0;
  }
  .addBtn {
    color: white;
    vertical-align: middle;
  }
</style>
