<template>
  <div id="board">
      <BoardHeader/>
      <BoardCreate v-on:boardCreate="boardCreate" />
      <BoardList v-bind:propsdata="list"/>
  </div>
</template>

<script>
import BoardCreate from './BoardCreate.vue'
import BoardHeader from './BoardHeader.vue'
import BoardList from './BoardList.vue'
export default {
    components: {
        BoardHeader,
        BoardList,
        BoardCreate,
    },
    data() {
        return{
            list: [],
        }
    },
    created(){
        this.getList()
    },
    methods : {
        boardCreate(data){
            window.console.log(data);
            this.$axios.post('http://localhost:8080/board/insert/'+data.id+'/'+data.title+'/'+data.content)
                .then((res)=>{
                    window.console.log(res);
                    this.list = [];
                    this.getList();
                })
                .catch((err)=>{
                    window.console.log(err);
                })
        },
        getList(){
            this.$axios.get("http://localhost:8080/board", { 
                headers:{
                    'Access-Control-Allow-Origin': '*',
                }
             })
            .then((res)=>{
                var data = JSON.parse(JSON.stringify(res.data));
                data.forEach((row)=>{
                    this.list.push(row);
                })
            })
            .catch((err)=>{
                window.console.log("에러 : "+err);
            })
        }
    }
}
</script>

